import { Dispatch } from "react";
import { AppActions, AppState } from "../_types";
import {
  FAVORITE_SUCCESS,
  IFavorite,
  INFO_FAILURE,
  INFO_REQUEST,
  INFO_SUCCESS,
  IPlaytime,
  PLAYTIME_SUCCESS,
} from "../_types/profile.types";
import { errorHandler, seccessHandler, loginErrorHandler } from "../components/Layout/SnackBar/alert";
import { API } from "../_helpers/api";

export const profileRequest = () => (
  (dispatch: Dispatch<AppActions>) => {
    dispatch({ type: INFO_REQUEST });
    API.get(`/jwt-auth/v1/profile`)
      .then(res => {
        const { favorite, ...rest } = res.data
        dispatch({
          type: INFO_SUCCESS,
          profile: rest,
        });
        dispatch({
          type: FAVORITE_SUCCESS,
          favorite,
        });
      })
      .catch(err => errorHandler(err, INFO_FAILURE))
  }
)
export const updateProfile = (user: any) => (
  (dispatch: Dispatch<AppActions>) => {
    const requestUpdate = (userData: any) => (
      API.put(`/jwt-auth/v1/profile`, userData)
        .then(res => {
          dispatch({
            type: INFO_SUCCESS,
            profile: { ...res.data },
          });
        })
        .catch(err => errorHandler(err, INFO_FAILURE))
    )
    dispatch({ type: INFO_REQUEST });
    if (user.image_select) {
      var data = new FormData();
      data.append("file", user.image_select);
      API.post("wp/v2/media", data)
        .then((res) => {
          requestUpdate({ ...user, image_select: String(res.data.id) })
        })
        .catch((err) => errorHandler(err, INFO_FAILURE));

    } else { requestUpdate(user) }
  }
)
export const getPlayTimes = () => (
  (dispatch: Dispatch<AppActions>) => {
    dispatch({ type: INFO_REQUEST });
    API.get(`/jwt-auth/v1/user?for=playtime`)
      .then(res => {
        dispatch({
          type: PLAYTIME_SUCCESS,
          playtime: res.data,
        });
      })
      .catch(err => errorHandler(err, INFO_FAILURE))
  }
)
export const addFavorites = (data: IFavorite) => (
  (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const isLogged = getState().auth.loggedIn
    if (isLogged) {
      dispatch({ type: INFO_REQUEST });
      API.put(`/jwt-auth/v1/user?for=favorite`, data)
        .then(res => {
          dispatch({
            type: FAVORITE_SUCCESS,
            favorite: res.data,
          });
        })
        .catch(err => errorHandler(err, INFO_FAILURE))
    } else {
      loginErrorHandler(INFO_FAILURE);
    }
  }
)
export const addPlayTimes = (data: IPlaytime) => (
  (dispatch: Dispatch<AppActions>, getState: () => AppState) => {
    const isLogged = getState().auth.loggedIn
    if (isLogged) {
      API.put(`/jwt-auth/v1/user?for=playtime`, data)
        .then(res => {
          dispatch({
            type: PLAYTIME_SUCCESS,
            playtime: res.data,
          });
          seccessHandler("Saved")
        })
        .catch(err => errorHandler(err, INFO_FAILURE))
    } else {
      loginErrorHandler(INFO_FAILURE);
    }
  }
)