import {
  INFO_REQUEST,
  INFO_FAILURE,
  INFO_SUCCESS,
  DELETE_PROFILE,
  PLAYTIME_SUCCESS,
  ProfileActionTypes,
  DefaultProfileState,
  FAVORITE_SUCCESS,
} from "../_types/profile.types";

const profileReducerDefaultState: DefaultProfileState = {
  loading: true,
  error: "User not found!"
};

export function profileReducer(
  state = profileReducerDefaultState,
  action: ProfileActionTypes
): DefaultProfileState {
  switch (action.type) {
    case INFO_REQUEST:
      return { ...state, loading: true };
    case INFO_SUCCESS:
      return {
        ...state,
        loading: false,
        data: action.profile,
      };
    case INFO_FAILURE:
      return {
        loading: false,
        error: action.error,
      };
    case PLAYTIME_SUCCESS:
      return {
        ...state,
        loading: false,
        playtime: action.playtime,
      };
    case FAVORITE_SUCCESS:
      return {
        ...state,
        loading: false,
        favorite: action.favorite,
      };
    case DELETE_PROFILE:
      return {
        loading: true,
        error: "User not found!",
      };
    default:
      return state;
  }
}
