import React, { useEffect } from "react";
import { Avatar, createStyles, makeStyles, Theme } from "@material-ui/core";
import clsx from "clsx";
import {
  AccountCircle,
  Star,
  Notifications,
  MoreHoriz,
} from "@material-ui/icons/";
import Badge from "@material-ui/core/Badge";
import IconButton from "@material-ui/core/IconButton";
import { AppBarInterface } from "./MainAppBar";
import { store } from "../../../_store/store.config";
import { profileRequest } from "../../../_actions/profile.actions";
import { INFO_FAILURE } from "../../../_types/profile.types";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    sectionDesktop: {
      display: "none",
      [theme.breakpoints.up("sm")]: {
        display: "flex",
      },
    },
    sectionMobile: {
      display: "flex",
      [theme.breakpoints.up("sm")]: {
        display: "none",
      },
    },
    avatarSmall: {
      color: theme.palette.text.primary,
      backgroundColor: theme.palette.background.paper,
      width: theme.spacing(3),
      height: theme.spacing(3),
    },
    imageBorder: {
      border: `2px solid ${theme.palette.text.primary}`,
    },
  })
);

const IconController = ({
  state,
  dispatch,
  loggedIn,
  profile,
}: AppBarInterface) => {
  useEffect(() => {
    if (loggedIn) {
      if (!profile) store.dispatch(profileRequest());
    } else {
      store.dispatch({ type: INFO_FAILURE });
    }
  }, [loggedIn, profile]);
  const classes = useStyles();
  const { menuId, mobileMenuId } = state;
  const profileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    dispatch({ type: "profile-anchor", payload: event.currentTarget });
  };
  const mobileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    dispatch({ type: "mobile-anchor", payload: event.currentTarget });
  };
  const image = profile?.image_select;
  return loggedIn ? (
    <React.Fragment>
      <div className={classes.sectionDesktop}>
        <IconButton aria-label="show 4 new mails" color="inherit">
          <Badge badgeContent={4} color="secondary">
            <Star />
          </Badge>
        </IconButton>
        <IconButton aria-label="show 17 new alerts" color="inherit">
          <Badge badgeContent={17} color="secondary">
            <Notifications />
          </Badge>
        </IconButton>
        <IconButton
          edge="end"
          aria-label="account of current user"
          aria-controls={menuId}
          aria-haspopup="true"
          onClick={profileMenuOpen}
          color="inherit"
        >
          <Avatar
            className={clsx(classes.avatarSmall, {
              [classes.imageBorder]: image,
            })}
            src={image ? image : undefined}
          >
            <AccountCircle />
          </Avatar>
        </IconButton>
      </div>
      <div className={classes.sectionMobile}>
        <IconButton
          aria-label="show more"
          aria-controls={mobileMenuId}
          aria-haspopup="true"
          onClick={mobileMenuOpen}
          color="inherit"
        >
          <MoreHoriz />
        </IconButton>
      </div>
    </React.Fragment>
  ) : (
    <IconButton
      edge="end"
      aria-label="account of current user"
      aria-controls={menuId}
      aria-haspopup="true"
      onClick={profileMenuOpen}
      color="inherit"
    >
      <AccountCircle />
    </IconButton>
  );
};
export default IconController;
