import { LoginResponse } from "./auth.types";

export const INFO_REQUEST = "PROFILE_INFO_REQUEST";
export const INFO_SUCCESS = "PROFILE_INFO_SUCCESS";
export const INFO_FAILURE = "PROFILE_INFO_FAILURE";

export const PLAYTIME_SUCCESS = "PLAYTIME_SUCCESS";
export const FAVORITE_SUCCESS = "FAVORITE_SUCCESS";

export const SAVE_PROFILE = "SAVE_PROFILE";
export const DELETE_PROFILE = "DELETE_PROFILE";

export interface DefaultProfileState {
  loading: boolean;
  error: string;
  data?: IProfile;
  playtime?: IPlaytime[];
  favorite?: IFavorite[];
}
export interface IProfile {
  id: string;
  name: string;
  nickname: string;
  first_name: string;
  last_name: string;
  memberships: {
    status: string;
    expire: string;
    level_id: string;
  };
  slug: string;
  title: { rendered: string };
  score: { rendered: string };
  image_select: string;
  mobile: string;
  info?: LoginResponse;
}
export interface IPlaytime {
  song: {
    name: string;
    id: number;
    notes: number;
  };
  time: string;
  score: string;
  date: string;
  progress: string;
}

export interface IFavorite {
  id: number;
  title: string;
  type: string;
  excerpt: string;
  author: string;
  featured_img: string;
}

export interface SaveProfileInfoAction {
  type: typeof SAVE_PROFILE;
  userData: any;
}
export interface DeleteProfileInfoAction {
  type: typeof DELETE_PROFILE;
}

export interface ProfileInfoRequestAction {
  type: typeof INFO_REQUEST;
}
export interface ProfileInfoSuccessAction {
  type: typeof INFO_SUCCESS;
  profile: IProfile;
}
export interface ProfileInfoFailureAction {
  type: typeof INFO_FAILURE;
  error: string;
}

export interface UserPlayTimeSeccess {
  type: typeof PLAYTIME_SUCCESS;
  playtime: IPlaytime[];
}
export interface UserFavoritesSeccess {
  type: typeof FAVORITE_SUCCESS;
  favorite: IFavorite[];
}

export type ProfileActionTypes =
  | SaveProfileInfoAction
  | DeleteProfileInfoAction
  | ProfileInfoRequestAction
  | ProfileInfoSuccessAction
  | ProfileInfoFailureAction
  | UserPlayTimeSeccess
  | UserFavoritesSeccess;
