export const cauntMeasures = (
  rythm: number,
  storeDrums: any[],
  storeTracks: any[],
  songdur: number = 0
) => {
  let mx: number = Math.ceil(songdur);
  const add: number = songdur ? 6 : 2;
  const mxT = Math.max.apply(
    Math,
    storeTracks.map((o) => o.beat + o.length)
  );
  const mxD = Math.max.apply(
    Math,
    storeDrums.map((o) => o.beat)
  );
  mx = mxT > mx ? mxT : mx;
  mx = mxD > mx ? mxD : mx;
  mx = mx + add;
  do {
    mx++;
  } while (mx % rythm !== 0);
  console.log(mx, songdur, mxD, mxT);
  return mx;
};
