import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import { ThunkDispatch } from "redux-thunk";
import { push, goBack } from "connected-react-router";
import { ArrowForward } from "@material-ui/icons";
import { Theme, makeStyles, createStyles } from "@material-ui/core/styles";
import {
  AppBar,
  Link,
  Paper,
  Toolbar,
  IconButton,
  Divider,
  Breadcrumbs,
} from "@material-ui/core";
import {
  AllInbox,
  LibraryMusic,
  QueueMusic,
  MusicNote,
  EmojiSymbols,
  Favorite,
} from "@material-ui/icons";
import { AppActions, AppState } from "../../_types";
import { pathFinder } from "../../_helpers/constants";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    bracrumbs: {
      padding: theme.spacing(1, 2, 0, 2),
      "& > ol > li": {
        padding: theme.spacing(1, 0.5),
      },
    },
    back: {
      margin: theme.spacing(0, 2),
    },
  })
);

const pages: any = {
  post: <AllInbox />,
  guide: <LibraryMusic />,
  course: <QueueMusic />,
  lesson: <MusicNote />,
  exercise: <EmojiSymbols />,
  favorite: <Favorite />,
};

interface IBreadcrumbs {
  pathname: string;
  single: any;
}

interface ILinksPath {
  name: "home";
  url: "/";
}

type Props = IBreadcrumbs & LinkDispatchProps;

const Breadcrumb = (props: Props) => {
  const { single, pathname, push, back } = props;

  const [linkPath, setLinkPath] = useState([] as ILinksPath[]);

  const [trigger, setTrigger] = useState(false);

  const classes = useStyles();

  useEffect(() => {
    const path = pathFinder(pathname);
    const keys = Object.keys(path);
    const linkArray: any[] = [];
    keys.forEach((key, i) => {
      const post = single[keys[i - 1]];
      const title = post?.data?.title;
      let name = i === 0 ? pages[key] : title ? title : "...";
      linkArray[i] = {
        url: pathname
          .split("/")
          .slice(0, i + 2)
          .join("/"),
        name: name,
      };
    });
    if (path.length > 0) {
      setTrigger(true);
      setLinkPath(linkArray);
    }
    return () => {
      setTrigger(false);
    };
  }, [pathname, single]);

  const handleClick = (
    event: React.MouseEvent<HTMLAnchorElement, MouseEvent>
  ) => {
    event.preventDefault();
    push(event.currentTarget.pathname);
  };

  return trigger ? (
    <AppBar position="static">
      <Paper square style={{ overflow: "hidden" }}>
        <Toolbar disableGutters>
          <IconButton
            edge="start"
            color="inherit"
            aria-label="menu"
            onClick={back}
            className={classes.back}
          >
            <ArrowForward />
          </IconButton>
          <Divider orientation="vertical" flexItem />
          <Breadcrumbs aria-label="breadcrumb" className={classes.bracrumbs}>
            {linkPath.map((link, i) => (
              <Link
                key={i + link.name}
                href={link.url}
                color="inherit"
                aria-label={link.name}
                onClick={handleClick}
              >
                {link.name}
              </Link>
            ))}
          </Breadcrumbs>
        </Toolbar>
      </Paper>
    </AppBar>
  ) : null;
};

interface LinkDispatchProps {
  push: (key: string) => void;
  back: () => void;
}

const mapStateToProps = (state: AppState, ownProps: {}): IBreadcrumbs => {
  const { pathname } = state.router.location;
  return {
    pathname,
    single: state.single,
  };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  push: bindActionCreators(push, dispatch),
  back: bindActionCreators(goBack, dispatch),
});

const conectedBreadcrumb = connect(
  mapStateToProps,
  mapDispatchToProps
)(Breadcrumb);

export default conectedBreadcrumb;
