import {
  PlayerActionTypes,
  REQUEST_PLAYER,
  RESET_PLAYER,
  ENTER_PLAYMODE,
  ENTER_NOTEMODE,
  EXIT_PLAYMODE,
  DRAW_GAME_PIANO,
  DRAW_NOTE_PIANO,
  DRAW_NOTES,
  IMelodition,
  DRAW_GAME,
  DRAW_FINGER,
  RESIZE_PLAYER,
  DRAW_SEEKBAR
} from "../_types/player.types";
const loadings = {
  fingerLoading: true,
  notesLoading: true,
  pianoLoading: true,
  gameLoading: true,
  seekLoading: true,
  loading: true,
};
const defaultPlayer: IMelodition = {
  ...loadings,
  playMode: false,
  noteMode: false,
  octave: 5,
  bpm: 4,
  songLength: 272,
  tapSize: 32,
};
const resetState: IMelodition = JSON.parse(JSON.stringify(defaultPlayer))

export const playerReducer = (
  state = defaultPlayer,
  action: PlayerActionTypes
): IMelodition => {
  switch (action.type) {
    case REQUEST_PLAYER:
      return { ...state, ...action.payload, loading: false };
    case DRAW_GAME_PIANO:
      return { ...state, ...action.payload, pianoLoading: false };
    case DRAW_NOTE_PIANO:
      return { ...state, ...action.payload, pianoLoading: false };
    case DRAW_GAME:
      return { ...state, gameLoading: false };
    case DRAW_FINGER:
      return { ...state, fingerLoading: false };
    case DRAW_NOTES:
      return { ...state, notesLoading: false };
    case DRAW_SEEKBAR:
      return { ...state, seekLoading: false };
    case ENTER_NOTEMODE:
      return { ...state, noteMode: true };
    case ENTER_PLAYMODE:
      return {
        ...state,
        ...loadings,
        loading: false,
        playMode: true,
        canvasSize: undefined
      };
    case EXIT_PLAYMODE:
      return {
        ...state,
        ...loadings,
        loading: false,
        playMode: false,
      };
    case RESIZE_PLAYER:
      return { ...state, pianoLoading: true, gameLoading: true, notesLoading: true };
    case RESET_PLAYER:
      return resetState;
    default:
      return state;
  }
};
