import {
  LOST_FAILURE,
  LOST_REQUEST,
  LOST_SUCCESS,
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGIN_FAILURE,
  REGISTER_FAILURE,
  REGISTER_REQUEST,
  REGISTER_SUCCESS,
  USER_LOGOUT,
  CHANGE_THEME,
  UserActionTypes,
  DefaultUserState,
} from "../_types/auth.types";
import { getUserLocal } from "../_helpers/constants";

const user = getUserLocal("user");
const storedTheme = localStorage.getItem("theme");
const theme = storedTheme === "dark" ? "dark" : "light"

const initialState: DefaultUserState = user
  ? { loggedIn: true, loading: false, user, theme }
  : { loggedIn: false, loading: false, theme };

export function authReducer(
  state = initialState,
  action: UserActionTypes
): DefaultUserState {
  switch (action.type) {
    case LOGIN_REQUEST:
      return { ...state, loading: true };
    case LOGIN_SUCCESS:
      return {
        ...state,
        loggedIn: true,
        loading: false,
        user: action.user,
      };
    case LOGIN_FAILURE:
      return { ...state, loading: false };
    case REGISTER_REQUEST:
      return { ...state, loggedIn: false, loading: true };
    case REGISTER_SUCCESS:
      return { ...state, loading: false };
    case REGISTER_FAILURE:
      return { ...state, loading: false };
    case LOST_REQUEST:
      return { ...state, loggedIn: false, loading: true };
    case LOST_SUCCESS:
      return { ...state, loading: false };
    case LOST_FAILURE:
      return { ...state, loading: false };
    case USER_LOGOUT:
      return { ...state, user: undefined, loggedIn: false, loading: false };
    case CHANGE_THEME:
      localStorage.setItem("theme", action.theme);
      return { ...state, theme: action.theme };
    default:
      return state;
  }
}
