import React, { ComponentType, useReducer } from "react";
import { ThunkDispatch } from "redux-thunk";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { AppState } from "../../../_types";
import { AppActions } from "../../../_types";
import MainAppBar from "./MainAppBar";
import { changeTheme, logoutRequest } from "../../../_actions/user.actions";
import HeaderReducer, { initialState } from "../../../_reducers/header.reducer";
import { IProfile } from "../../../_types/profile.types";

interface MapProps {
  loggedIn: boolean;
  profile?: IProfile;
  theme: "light" | "dark";
}

export type IHeaderProps = LinkDispatchProps & MapProps;

const Header: React.FC<IHeaderProps> = ({ ...rest }) => {
  const [state, dispatch] = useReducer(HeaderReducer, initialState);

  return <MainAppBar state={state} dispatch={dispatch} {...rest} />;
};

interface LinkDispatchProps {
  logout: () => void;
  changeTheme: (theme: "light" | "dark") => void;
}

const mapStateToProps = (state: AppState): MapProps => {
  const { loggedIn, theme } = state.auth;
  return { loggedIn, theme, profile: state.user.data };
};

const mapDispatchToProps = (
  dispatch: ThunkDispatch<any, any, AppActions>
): LinkDispatchProps => ({
  logout: bindActionCreators(logoutRequest, dispatch),
  changeTheme: bindActionCreators(changeTheme, dispatch),
});

export default (connect(
  mapStateToProps,
  mapDispatchToProps
)(Header) as unknown) as ComponentType<unknown>;
