import Axios from "axios";
import { store } from "../_store/store.config";
import { logoutRequest } from "../_actions/user.actions";

export const authHeader = (): HeadersInit => {
  const localUser = localStorage.getItem("user");
  let user: { token: string } = localUser ? JSON.parse(localUser) : null;
  if (user?.token) {
    return { Authorization: "Bearer " + user.token };
  } else {
    return { Authorization: "" };
  }
}
export const API = Axios.create({
  baseURL: "https://panel.melodition.com/wp-json",
  headers: authHeader(),
});


API.interceptors.response.use(
  res => {
    return res;
  },
  err => {
    console.log(err)
    if (err.response.status === 403) {
      if (/invalid_token/.test(err.response.data.code))
        store.dispatch(logoutRequest())
      else if (/incorrect_password/.test(err.response.data.code))
        err.response.data.button = { url: "/lostpassword", msg: "بازیابی رمز" }
    }
    return Promise.reject(err);
  }
)
export const AudioApi = Axios.create({ method: "get", responseType: "arraybuffer" });
