import React, { Component, ComponentType, lazy, Suspense } from "react";
import { create } from "jss";
import jssRTL from "jss-rtl";
import { connect } from "react-redux";
import { ConnectedRouter } from "connected-react-router";
import { SnackbarProvider } from "notistack";
import { history } from "./_store/store.config";
import AppRouter from "./router";
import "./App.css";
import {
  Box,
  createStyles,
  jssPreset,
  Paper,
  responsiveFontSizes,
  Slide,
  StylesProvider,
  Theme,
  ThemeProvider,
  withStyles,
  WithStyles,
} from "@material-ui/core";
import Notifier from "./components/Layout/SnackBar/Notifier";
import Breadcrumb from "./components/Layout/Breadcrumbs";
import { LightTheme, DarkTheme } from "./components/Layout/Themes";
import Header from "./components/Layout/Header/Header";
import { AppState } from "./_types";
import { LoadingCircle } from "./components/Layout/Loadings";

const LessonPlayerMode = lazy(
  () => import("./components/Layout/Instruments/Piano/PlayMode/LessonPlayMode")
);

const jss = create({ plugins: [...jssPreset().plugins, jssRTL()] });

const useStyles = (theme: Theme) =>
  createStyles({
    app: {
      width: "100vw",
      height: "100vh",
      overflowX: "hidden",
      display: "flex",
      flexDirection: "column",
      overflow: "hidden",
    },
    mainBody: {
      height: "inherit",
      overflowX: "hidden",
      overflowY: "auto",
    },
  });

interface AppProps extends WithStyles<typeof useStyles> {
  playMode: boolean;
  theme: "light" | "dark";
}
type Props = AppProps & LinkDispatchProps;

class App extends Component<Props> {
  componentDidMount() {
    if (typeof window.orientation === "undefined")
      import("./_actions/midi.actions").then(({ requsetMidi }) => {
        requsetMidi();
      });
  }
  render() {
    const { theme, playMode, classes } = this.props;
    const currentTheme = theme === "light" ? LightTheme : DarkTheme;
    const responsTheme = responsiveFontSizes(currentTheme);
    return (
      <ConnectedRouter history={history}>
        <StylesProvider jss={jss}>
          <ThemeProvider theme={responsTheme}>
            <SnackbarProvider>
              <Slide direction="right" in={playMode} mountOnEnter unmountOnExit>
                <Paper className={classes.app}>
                  <Suspense fallback={<LoadingCircle />}>
                    <LessonPlayerMode />
                  </Suspense>
                </Paper>
              </Slide>
              <Box className={classes.app}>
                <Header />
                <Breadcrumb />
                <Notifier />
                <Box id="main-body" className={classes.mainBody}>
                  <AppRouter />
                </Box>
              </Box>
            </SnackbarProvider>
          </ThemeProvider>
        </StylesProvider>
      </ConnectedRouter>
    );
  }
}

interface LinkDispatchProps {}

const mapStateToProps = (state: AppState, ownProps: Props) => {
  const { theme } = state.auth;
  const { playMode } = state.melodition;
  return { theme, playMode, classes: ownProps.classes };
};

const connectedApp = (connect(
  mapStateToProps,
  {}
)(withStyles(useStyles)(App)) as unknown) as ComponentType<unknown>;

export default connectedApp;
