import { createMuiTheme } from "@material-ui/core";

export const globalStyle = createMuiTheme({
  direction: "rtl",
  typography: {
    fontFamily: "Vazir",
  },
  overrides: {
    MuiBreadcrumbs: {
      root: {
        width: "calc(100vw - 64px)",
      },
      ol: {
        flexWrap: "nowrap",
        overflowX: "scroll",
        scrollbarWidth: "none",
        whiteSpace: "nowrap",
        "&::-webkit-scrollbar": {
          display: "none",
        },
      },
    },
    MuiCardActions: {
      root: {
        padding: undefined,
      },
    },
    MuiIconButton: {
      colorSecondary: {
        "&:hover": {
          backgroundColor: "rgb(200 200 200 / 0.5)",
        },
      },
    },
  },
});
