import React from "react";
import {
  CircularProgress,
  createStyles,
  makeStyles,
  Theme,
} from "@material-ui/core";

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    loading: {
      display: "flex",
      height: "83%",
      boxShadow: "inset 0 1px 4px 0px black",
    },
    loadingPad: {
      display: "flex",
      width: "100vw",
      minHeight: "calc(100vh - 300px)",
      overflow: "hidden",
    },
  })
);

interface LoadingProps {
  noPad?: boolean;
}

export const LoadingCircle: React.FC<LoadingProps> = ({ noPad }) => {
  const classes = useStyles();
  return (
    <div className={noPad ? classes.loading : classes.loadingPad}>
      <CircularProgress color="secondary" style={{ margin: "auto" }} />
    </div>
  );
};
